    },
    scriptSrc: {
      // jsçæ¬æº
      required: false,
      type: [String],
      default: 'https://g.alicdn.com/de/prismplayer/2.9.3/aliplayer-min.js'
    }
  },
  setup(props) {
    const playerId = ref('player' + props.id)
    const player = ref(null)
    const events = reactive(statusEvent)
    const state = reactive({
      config: {} // éç½®
    })
    // è§é¢å®ä¾
    const getPlayer = () => {
      return player.value
    }
    // æ¾å¨è§é¢åå§åæé®æ¸²æå®æ¯
    const ready = () => {
      player.value && player.value.ready()
    }
    /**
     * æ­æ¾è§é¢ã
     */
    const play = () => {
      player.value && player.value.play()
    }
    /**
     * å½åè§é¢ç¶æ
     */
    const getStatus = () => {
      return player.value && player.value.getStatus()
    }
    /**
     * æåè§é¢
     *
     */
    const pause = () => {
      player.value && player.value.pause()
    }

    const initPlayer = () => {
      console.log(window.Aliplayer, '------------window.Aliplayer')
      if (player.value === null) {
        const options = props.options
        if (options) {
          for (const key in options) {
            state.config[key] = options[key]
          }
        }
        if (props.source) {
          state.config.source = props.source // æ­æ¾æº
        }
        state.config.id = playerId.value
        player.value = window.Aliplayer(state.config)
      } else {
        player.value && player.value.replay() // éæ¯åéæ­
      }
    }
    const init = () => {
      const linkID = 'aliplayer-min-css'
      const scriptID = 'aliplayer-min-js'
      const head = document.getElementsByTagName('head')
      const html = document.getElementsByTagName('html')
      let scriptTag = document.getElementById(scriptID)
      const linkIDTag = document.getElementById(linkID)
      if (!linkIDTag) {
        const link = document.createElement('link')
        link.type = 'text/css'
        link.rel = 'stylesheet'
        link.href = props.cssLink
        link.id = linkID
        head[0].appendChild(link)
      }
      if (!scriptTag) {
        scriptTag = document.createElement('script')
        scriptTag.type = 'text/javascript'
        scriptTag.id = scriptID
        scriptTag.src = props.scriptSrc
        html[0].appendChild(scriptTag)
        // å¼å®¹åé¡µå è½½åç¡¬å è½½
        scriptTag.addEventListener('load', () => {
          initPlayer()
        })
      } else {
        // è¿æ ·æ¯ä¸ºäºå¼å®¹é¡µé¢ä¸æå¤ä¸ªæ­æ¾å¨
        scriptTag.addEventListener('load', () => {
          initPlayer()
        })
      }
    }
    /**
     * ç´æ¥æ­æ¾è§é¢urlï¼timeä¸ºå¯éå¼ï¼åä½ç§ï¼ãç®ååªæ¯æåç§æ ¼å¼ï¼mp4/flv/m3u8ï¼ä¹é´åæ¢ã
     * æä¸æ¯æç´æ­rtmpæµåæ¢ã
     * @return player
     */
    const loadByUrl = (url, time) => {
      // console.log(`ç´æ¥æ­æ¾è§é¢url${url}ï¼timeä¸º${time}ã`);
      this.player && this.player.loadByUrl(url, time)
    }
    watch(
      () => state.config.source,
      url => {
        loadByUrl(url)
      }
    )
    onUnmounted(() => {
      // é²æ­¢éå¤åå»º
      const head = document.querySelector('head') // ç§»é¤ææçéå¤åå»ºçæ ç­¾
      const nodes = document.querySelectorAll(
        'script[src="https://g.alicdn.com/de/prismplayer/2.9.3/aliplayer-min.js"]'
      )
      head &&
        nodes.forEach(item => {
          head.removeChild(item)
        })
    })
    onMounted(() => {
      if (window.Aliplayer === undefined) {
        init()
      } else {
        initPlayer()
      }
    })
    return {
      getPlayer,
      playerId,
      ready,
      pause,
      play,
      getStatus,
      events
